.secao_produtos{
    width: 100%;
    text-align: center;
    padding: 100px 20px;
}
#Cards{
    margin: 50px 0;
    display: flex;
    flex-wrap: wrap;
}
.Card{
    background-color: #f6f6f6;
    width: calc(50% - 15px);
    padding: 20px 10px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.Card h3{
    margin: 0;
    font-size: 32px;
}
.Card img{
    width: 80%;
    height: 150px;
    margin: 30px;
    object-fit: contain;
    box-sizing: border-box;
}
.Card p{
    margin: 0;
    font-weight: bolder;
    font-size: 22px;
}
.secao_produtos ul{
    padding: 0 20px;
    text-align: center;
}
.secao_produtos ul li{
    text-align: center;
    margin: 5px;
    list-style-position: inside;
}
@media (max-width: 768px){
    .secao_produtos{
        padding: 60px 20px;
    }
    .Card h3{
        font-size: 22px;
    }
    .Card img{
        max-height: 100px;
    }
    .Card p{
        font-size: 16px;
    }
}
@media(max-width: 550px){
    .secao_produtos{
        padding-left: 10px;
        padding-right: 10px;
    }
    .Card{
        width: 100%;
    }
    ul{
        display: flex;
        flex-direction: column;
    }
    .secao_produtos ul li{
        text-align: left;
    }
}