header{
    padding: 10px 15px;
}
nav{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}
nav img{
    width: 150px;
}
ul a{
    font-size: 18px;
    color: #000;
    text-decoration: none;
    padding: 5px;
    margin: 0 5px;
}
@media(max-width: 550px){
    nav{
        flex-direction: column;
    }
    ul{
        width: 100%;
        text-align: center;
        padding: 10px;
        margin-top: 10px;
        border-top: 1px solid #aaadaf;
    }
}