#Section_sobre{
    background-color: #f8f7ff;
    padding: 100px 20px;
    text-align: center;
}
.quem-somos p{
    line-height: 32px;
}
.conteudo-somos{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
}
.conteudo-somos img, .conteudo-somos .explicacao{
    width: 50%;
    max-height: 300px;
    border: 1px solid #f3bc09;
}
.conteudo-somos img{
    object-fit: cover;
}
.conteudo-somos .explicacao{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 20px 25px;
    justify-content: space-around;
}
.explicacao h2{
    font-size: 36px;
    font-family: 'Anybody', cursive;
    letter-spacing: 5px;
    line-height: 42px;
}
.explicacao p{
    font-size: 22px;
    color: #4a4a4a;
}
@media (max-width: 768px){
    #Section_sobre{
        padding: 60px 20px;
    }
    .explicacao{
        padding: 15px;
    }
    .explicacao h2{
        font-size: 22px;
    }
    .explicacao p{
        font-size: 16px;
    }
}
@media(max-width: 550px){
    .Section_sobre{
        padding: 0 10px;
    }
    .conteudo-somos{
        display: flex;
        flex-wrap: wrap;
    }
    .conteudo-somos .explicacao{
        width: 100%;
        height: 300px;
    }
    .conteudo-somos img{
        width: 100%;
        height: 300px;
    }
}