#Section_contado{
    width: 100%;
    text-align: center;
    padding: 100px 20px;
    color: #fff;
    background-color: #05031b;
}
.contatos{
    display: flex;
    justify-content: center;
    align-items: center;
}
.imagem{
    display: flex;
    align-items: center;
    text-align: left;
    margin: 5px 0;
}
.imagem img{
    width: 18px;
}
.div-contato{
    text-align: left;
    padding-left: 15px;
}
.imagem p{
    font-size: 14px;
    margin-left: 10px;
}
.conteudo-contato .paragrafo{
   margin: 30px 0;
}
.div-contato h3{
    margin-bottom: 20px;
}
@media (max-width: 768px){
    #Section_contado{
        padding: 60px 20px;
    }
}
@media(max-width: 550px){
    #Section_contado{
       padding-left: 10px;
       padding-right: 10px;
    }
    .contatos{
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    .div-contato .imagem{
       width: 200px;
       padding-left: 10px;
    }
    .div-contato{
        margin-bottom: 20px;
        text-align: center;
        padding: 0;
    }
    .div-sociais{
        text-align: left;
    }
}