.img-fundo{
    background-repeat: no-repeat;
    background:linear-gradient(47deg, #000113 0%, #04061D 80%, #04061D 100%) ;
    height: 80vh;
    background-image: url("/public/assets/capa.png");
    background-position: center;
    background-size: cover;
}
.img-fundo div{
    width: 100%;
    height: 100%;
    padding: 20px;
    color: aqua;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.img-fundo div p, .img-fundo div h1{
    max-width: 500px;
    font-family: 'Anybody',cursive;
}
.img-fundo div p{
    font-size: 36px;
    color: rgb(196, 196, 196);
}
.img-fundo div h1{
    font-size: 60px;
    color: #fff;
    letter-spacing: 5px;
    line-height: 70px;
}
@media (max-width: 768px){
    .img-fundo div h1{
        font-size: 40px;
        max-width: 350px;
    }
    .img-fundo div p{
        font-size: 20px;
    }
}
@media(max-width: 550px){
    .img-fundo{
        background-position: 89% center;
    }
    .img-fundo div{
        background-color: rgba(0,0,0,0.6);
    }
}